@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

// обводка конейнеров, только на время разработки. Выкличть в config

@mixin outliner {
  outline: 1px dotted green;

  @include respond-to(medium) {
    outline-color: red;
  }

  @include respond-to(large) {
    outline-color: blue;
  }
}

// mixin для адпатива
// @include respond-to(medium) {
//    rules here...
//  }

@mixin respond-to($breakpoint) {
  @media (min-width: #{map-get($breakpoints, $breakpoint)}) {
    @content;
  }
}

@mixin headline-3 {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
}

@mixin headline-4 {
  font-size: 16px;
  line-height: 24px;
}

@mixin headline-6 {
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
}

@mixin tab-label {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-feature-settings: "cpsp" on;
}
